<template>
    <div class="choice">
        <div>
            <div class="choice_screen">
                <p>{{select}} | {{home}}</p>
                <div @click='screen_tap'>
                    <p>筛选</p>
                    <img src="../../../assets/home_img/caidan.png" alt="">
                </div>
            </div>
            <div class="choice_box" v-if="choice_isshow">
                <div v-for="(item, index) in choice_data" :key="index">{{item.name}}:
                    <span v-for="(i, index) in item.data" :key="index">{{i}}</span>
                </div>
                <div @click='choice_btn_tap' class="choice_btn">确认</div>
            </div>
        </div>
        <main>
            <div class="choice_list" v-for="(choice_item, index) in list_data" :key="index">
                <div @click='img3D_tap(index)' class="choice_img">
                    <img class="backimg" :src="choice_item.picture_url" alt="">
                    <img class="floatimg" src="../../../assets/home_img/vr_icon_1.png" alt="">
                </div>
                <div class="choice_cont">
                    <div>
                        <h3>{{choice_item.style_name}}</h3>
                        <span>装修总价{{choice_item.all_price}}</span>
                    </div>
                    <div>
                        <span>{{choice_item.title}}</span>
                        <span>{{choice_item.apartment_name}}</span>
                        <div>
                            <img src="../../../assets/home_img/chakan.png" alt="">
                            <span>{{choice_item.area_number}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    </div>
</template>

<script>
  import reuqest from "@/request.js";
    export default {
        data() {
            return {
                choice_isshow: false,
                select: '未选',
                home: '请选择户型',
                choice_data: [{
                    name: '户型',
                    data: ['一居室', '别墅']
                }, {
                    name: '风格',
                    data: ['现代风格', '田园风格']
                }],
                list_data: [],
            }
        },
        created() {
            reuqest({
                url: '/api/getThreeCaseEffectList',
                method: "post",
            }).then(data => {
                if (data.status == 200) {
                    this.list_data = data.data.data
                }
            })
        },
        methods: {
            screen_tap() {
                this.choice_isshow = !this.choice_isshow
            },
            choice_btn_tap() {
                this.select = '已选'
            },
            img3D_tap(i){
                window.open(this.list_data[i].panorama_link)
            }
        },
    }
</script>

<style scoped>
    .choice_screen {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        background: #e0e0e0;
        font-size: 0.8rem;
        color: #616161;
    }

    .choice_screen>p {
        height: 2.3rem;
        line-height: 2.3rem;
        padding: 0 0.6rem;
        flex: 4;
    }

    .choice_screen>div {
        height: 2.3rem;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        font-size: 1rem;
        flex: 1;
        background: #FD2691;
        color: white;
    }

    .choice_screen>div img {
        width: 1.2rem;
        height: 1.2rem;
    }

    .choice_box {
        font-size: 0.9rem;
        background-color: white;
        padding: 0 0.6rem 0.6rem;
    }

    .choice_box>div {
        padding: 0.5rem 0;
    }

    .choice_box span {
        margin: 0 0.6rem;
        color: #808080;
    }

    .choice main {
        overflow: hidden;
        padding: 0.8rem 0.3rem 0;
    }

    .choice_btn {
        color: white;
        background-color: #FD2691;
        width: 6rem;
        font-size: 1rem;
        height: 1.2rem;
        text-align: center;
        line-height: 1.2rem;
        border-radius: 0.3rem;
        margin: 0.5rem auto;
    }

    .choice_list {
        margin: 1%;
        width: 48%;
        font-size: 0;
        display: inline-block;
        box-sizing: border-box;
        border: 1px solid #d9d9d9;
        background: white;
    }

    .choice_img {
        position: relative;
    }
    .choice_img .backimg {
        width: 100%;
        height: 100%;
    }

    .choice_img .floatimg {
        position: absolute;
        right: 0.2rem;
        bottom: 0.5rem;
    }

    .choice_cont {
        padding: 0.2rem 0.3rem 0.3rem;
        color: #3c3c3c;
    }

    .choice_cont h3 {
        font-weight: normal;
        font-size: 0.9rem;
    }

    .choice_cont>div:first-child {
        margin-bottom: 0.3rem;
    }

    .choice_cont>div {
        font-size: 0.7rem;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    .choice_cont div img {
        margin-right: 0.2rem;
        width: 0.7rem;
        height: 0.5rem;
    }
</style>


