<template>
  <div class="panorama">
    <nav>
      <ul>
        <li @click='nav_Tap(index)' v-for='(item,index) in nav_list' :key='index' :class="index==active_nav?'active':''">{{item}}
        </li>
      </ul>
    </nav>
    <main>
      <section v-if='active_nav==0'>
          <panorama_3D></panorama_3D>
      </section>
      <section v-if='active_nav==1'>
          <plane_effect/>
      </section>
      <section v-if='active_nav==2'>
        <style_introduce/>
      </section>
      <section v-if='active_nav==3'>
        <style_test/>
      </section>
    </main>
  </div>
</template>

<script>
  import panorama_3D from './panorama-view/panorama-3D.vue'
  import plane_effect from './panorama-view/plane-effect.vue'
  import style_introduce from './panorama-view/style-introduce.vue'
  import style_test from './panorama-view/style-test.vue'
  
  export default {
    components:{panorama_3D,plane_effect,style_introduce,style_test},
    data() {
      return {
        active_nav:0,
        nav_list:['3D全景图集','平面效果图集','风格介绍','风格测试']
      }
    },
    methods: {
      nav_Tap(index){
        console.log(index)
        this.active_nav=index
      }
    },
  }
</script>

<style scoped>
  .panorama{
    background: #f0f0f0;
  }
  nav ul{
    overflow: hidden;
    padding: 0.5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  nav ul li {
    font-size: 0.9rem;
    background-color: white;
    padding: 0.3rem 0.4rem;
    border: 2px solid #FD2692;
    border-radius: 0.3rem;
  }

  .active {
    background-color: #2BBF4A;
    color: white;
    border: 2px solid #2BBF4A;
  }
</style>


